@import '~antd/dist/antd.css';

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

/*Custom CSS Clases Here*/

.ant-form-item-label {
  width: 100px;
}

.MeasurementInput {
  width: 142px;
}

.MeasurementInput input {
  text-align: right;
}

.MeasurementInput > .ant-input-number-handler-wrap {
  right: initial;
  border-radius: 2px 0 0 2px;
}

.MeasurementInput .ant-input-number-handler {
  border-left: initial;
  border-right: 1px solid #d9d9d9;
}

.MeasurementInput .ant-select-selector {
  width: 66px !important;
}
